  font-weight: lighter;
  margin-bottom: 1.8em;
  text-transform: capitalize; }

.list li {
  font-size: .88em;
  margin-bottom: .86em;
  font-weight: lighter; }
  .list li a {
    font-weight: bold;
    opacity: .9;
    text-decoration: none;
    -webkit-transition: opacity 150ms;
    transition: opacity 150ms; }
    .list li a:hover {
      opacity: 1; }

.container-inner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1; }



