.header-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0 90px;
  min-height: 120px;
  max-height: 120px; }

.logo {
  font-weight: bold;
  font-size: 1.12em; }
  .logo a {
    text-decoration: none; }

.divider {
  margin: 0 2em;
  height: 2.4em;
  width: 1px;
  background-color: rgba(255, 255, 255, 0.6); }

.item {
  font-size: .8em;
  font-weight: lighter;
  -webkit-transition: opacity 150ms;
  transition: opacity 150ms;
  cursor: pointer;
  opacity: .65;
  text-decoration: none;
  text-transform: capitalize; }
  .item:hover {
    opacity: 1; }
  .item.is-active {
    opacity: 1;
    cursor: default; }

.coma {
  opacity: .65; }



