.container {
  background-color: #1B1B1B;
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  opacity: 1;
  -webkit-transition: opacity 1s;
  transition: opacity 1s; }
  .container.is-hidden {
    opacity: 0; }

@-webkit-keyframes blink {
  0% {
    opacity: .3; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: .3; }
  100% {
    opacity: 1; } }

